
<template>
<div>
  <div v-show="nowTab === 1">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i>项目管理</el-breadcrumb-item>
        <el-breadcrumb-item>我的项目</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center;margin-top:20px;">
        <div class="handle-box">
          <el-button type="primary" icon="plus" class="mr10" @click="toadd()">创建项目</el-button>
          <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10" icon="search"></el-input>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <el-table :data="data" stripe style="width: 100%" v-loading="tableLoading">
            <el-table-column prop="projectName" label="项目名称">
            </el-table-column>
            <el-table-column prop="orderNums" label="订单数量">
            </el-table-column>
            <el-table-column prop="cilentCompanyName" label="客户单位">
            </el-table-column>
            <el-table-column prop="yuBaoJiaStatus" label="预报价状态">
                <template slot-scope="scope">
            <el-tag v-if="scope.row.yuBaoJiaStatus === '1'" >审核中</el-tag>
            <el-tag v-if="scope.row.yuBaoJiaStatus === '0'" type="info">无</el-tag>
            <el-tag v-if="scope.row.yuBaoJiaStatus === '3'" type="danger">驳回</el-tag>
            <el-tag v-if="scope.row.yuBaoJiaStatus === '2'" type="success">通过</el-tag>
          </template>
            </el-table-column>
            <el-table-column prop="shangWuStatus" label="商务状态">
                <template slot-scope="scope">
            <el-tag v-if="scope.row.shangWuStatus === '1'" >审核中</el-tag>
            <el-tag v-if="scope.row.shangWuStatus === '0'" type="info">无</el-tag>
            <el-tag v-if="scope.row.shangWuStatus === '3'" type="danger">驳回</el-tag>
            <el-tag v-if="scope.row.shangWuStatus === '2'" type="success">通过</el-tag>
          </template>
            </el-table-column>
            <el-table-column prop="orderSchedule" label="订单进度">
            </el-table-column>
            <el-table-column label="创建时间">
                <template slot-scope="scope">
                <span><i class="el-icon-time"></i>{{scope.row.createTime}}</span>
              </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="">查看详情</el-button>
          </template>
            </el-table-column>
        </el-table>
      </el-main>
    </el-container>
    <div class="block" style="float:right;margin-top:15px;">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 30, 80, 100]" :page-size="pagesize" layout="total,sizes, prev, pager, next, jumper" :total="totalnum">
      </el-pagination>
    </div>
  </div>
  <!-- 添加表单 -->
  <el-dialog title="项目信息" :visible.sync="dialogFormVisible" width='40%'>
      <div class="form-box">
          <el-form ref="projectfrom" :model="projectfrom" :rules="rules" label-width="120px">
              <el-form-item label="项目名称：" prop="projectName">
                  <el-input placeholder="请输入项目名称" v-model="projectfrom.projectName"></el-input>
              </el-form-item>
              <el-form-item label="客户单位：" prop="cilentCompanyName">
                  <el-input placeholder="请输入客户单位" v-model="projectfrom.cilentCompanyName"></el-input>
              </el-form-item>
              <el-form-item label="备注：" prop="remark">
                  <el-input type="textarea" v-model="projectfrom.remark"></el-input>
              </el-form-item>
          </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
          <el-button type="primary" :disabled="this.addloading" @click="addProject('projectfrom')">确 定</el-button>
          <el-button @click="dialogFormVisible = false">取 消</el-button>
      </div>
  </el-dialog>
</div>
</template>
<script>
export default {
  data() {
    return {
      nowTab: 1,
      username:'',
      name:'',
      uid:'',
      tableLoading: false,
      addloading: false,
      dialogFormVisible: false,
      projectfrom:{
        projectName:'',
        cilentCompanyName:'',
        remark:'',
        addUserId:'',
        addUserName:''
      },
      currentPage: 1,
      totalnum: 0,
      pagesize: 10,
      select_word: '',
      tableData: [],
      // 上部分
      rules: {
        projectName: [{
            required: true,
            message: '请输入项目名称',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ],
        cilentCompanyName: [{
            required: true,
            message: '请输入客户单位',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    toadd(){
      this.dialogFormVisible = true;
      this.addloading = false;
    },
    addProject(formName){
      this.addloading = true;
      let self = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          self.projectfrom.addUserId = self.uid;
          self.projectfrom.addUserName = self.name;
          this.postRequest("/project/list/add/project", self.projectfrom).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              var id = resp.data.data.id;
              this.dialogFormVisible = false;
              this.$message.success("添加成功")
              this.tableLoading = true;
              this.getCollectMouldList();
            }else{
              this.$message.error("添加失败")
            }
          })
        } else {
          return false;
        }
      });
    },

    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.getCollectMouldList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getCollectMouldList();
    },
    // 获取项目列表
    getCollectMouldList() {
      let _this = this
      this.getRequest("/project/list/listbypage?page=" + this.currentPage + "&size=" + this.pagesize).then(resp => {
        _this.tableLoading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          console.log(resp);
          _this.tableData = resp.data.data.projectlist
          _this.totalnum = resp.data.data.count
        }
      })
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      this.tableLoading = true;
      this.getCollectMouldList();
    })
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
  },
  computed: {
    data() {
      const self = this;
      return self.tableData.filter(function(d) {
        if (d.projectName.indexOf(self.select_word) > -1 ||
            d.cilentCompanyName.indexOf(self.select_word) > -1
        ) {
          return d;
        }
      });
    }
  }
}
</script>
<style scoped>
.handle-box {
  margin-bottom: 20px;
}
.el-select .el-input {
  width: 130px;
}
.card-bottom {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: right;
}
.input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
.card-bottom-left {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: left;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.mould-card {
  margin-bottom: 15px;
}

.mould-card:hover {
  transform: translateY(-2%);
  box-shadow: 1px 4px 10px 2px #CCC;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.pre-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
}
</style>



